import {
	getDOMElements,
    restCall,
    isDefined,
    isEmpty,
    replaceCurlyBraces
} from './Utilities.js';

import ItemCreatorIntegrations from './ItemCreatorIntegrations.js';

import ItemHeaderMenu from './ItemHeaderMenu.js';
import ItemHeaderIcon from './ItemHeaderIcon.js';
import ItemHeaderTag  from './ItemHeaderTag.js';

import Triggers 	  from './Triggers.js';
import Actions 		  from './Actions.js';

class itemCreatorTaskSelector {
	constructor( itemType, integrationId, $currentItem ){
		this.itemType 		= itemType;
		this.integrationId 	= integrationId;
		this.$currentItem 	= $currentItem;

		// Render DOM elements
		this.create();
	}

	create(){
		// Create container
		let container = $( '<div/>', { 'class': 'item' });
		this.$item = container;

		container.append( this.createHeader() );
		container.append( this.createTaskSelector() );

        this.$currentItem.replaceWith( container );
	}

	createHeader(){
		let header  = $( '<div/>' );

        let titleContainer = $( '<div/>', { 'class': 'item-title clear' }),
            titleContent   = $( '<div/>', { 'class': 'item-title__describe' }),
            title          = $( '<div/>', { 'class': 'item-title__non-editable' });

        // Define title text
        if ( this.itemType == 'trigger' ){
        	title.text( UncannyAutomator.i18n.trigger.select );
        }

        if ( this.itemType == 'action' ){
        	title.text( UncannyAutomator.i18n.action.select );
        }

        let actions        = new ItemHeaderMenu();
        header.append( actions );

        let icon 		   = new ItemHeaderIcon( this.integrationId );
        header.append( icon );

        let itemTag = new ItemHeaderTag( this.integrationId );
        titleContainer.append( itemTag );

        titleContent.append( title );
        titleContainer.append( titleContent );
        header.append( titleContainer );

        return header;
	}

	createTaskSelector(){
		let searchPlaceholder,
			globalItems;

		if ( this.itemType == 'trigger' ){
			searchPlaceholder  = UncannyAutomator.i18n.trigger.search;
			globalItems 	   = UncannyAutomator.triggers;
		}

		if ( this.itemType == 'action' ){
			searchPlaceholder  = UncannyAutomator.i18n.action.search;
			globalItems        = UncannyAutomator.actions;
		}

		// Create elements
		let container    = $( '<div/>', { 'class': 'item__content' }),
			itemOptions  = $( '<div/>', { 'class': 'item-options item-options--nopadding item-options--noborder' }),
			formElement  = $( '<div/>', { 'class': 'form-element' }),
			taskSelector = $( '<div/>', { 'class': 'item__task-selector' });

		// Search
		let search 		 = $( '<div/>', { 'class': 'item-task-selector__search' }),
			searchInput  = $( '<input/>', {
				'class':       'item-task-selector__search-input',
				'type':        'text',
				'placeholder': searchPlaceholder
			});

		search.append( searchInput );
		taskSelector.append( search );

		// Tasks
		let listContainer = $( '<div/>', { 'class': 'item-task-selector__options' });
		let tasks = [];

		// Filter by integration
		let globalItemsByIntegration = [];
		$.each( globalItems, ( index, globalItem ) => {
			if ( globalItem.integration == this.integrationId ){
				globalItemsByIntegration.push( globalItem );
			}
		});
		globalItems = globalItemsByIntegration;

		// Show items
		$.each( globalItems, ( index, globalItem ) => {
			let task 	 = $( '<div/>', { 'class': 'item-task-selector__option' }),
				sentence = replaceCurlyBraces( globalItem.select_option_name, '<span class="item-task-selector__option-variable">', '</span>' );

			task.append( sentence );
			listContainer.append( task );

			tasks.push( task );

			// Bind click
			task.on( 'click', () => {
				taskSelector.addClass( 'item__task-selector--loading' );
				task.addClass( 'item-task-selector__option--loading' );

				let restRequestCode;

				if ( this.itemType == 'trigger' ){
					restRequestCode = 'add-new-trigger';
				}

				if ( this.itemType == 'action' ){
					restRequestCode = 'add-new-action';
				}

				// Get default meta
				let defaultMeta = {}

				$.each( globalItem.options, ( index, option ) => {
					if ( isDefined( option.default_value ) ){
						defaultMeta[ option.option_code ] = option.default_value;
					}
				});

				restCall( 'add', {
                    recipePostID: UncannyAutomator.recipe.id,
                    action:       restRequestCode,
                    item_code:    globalItem.code,
                    default_meta: $.extend( {}, defaultMeta, {
                    	integration_name: UncannyAutomator.integrations[ this.integrationId ].name,
                    	sentence: 		  globalItem.sentence
                    })
                }, ( response ) => {
                	if ( this.itemType == 'trigger' ){
                		new Triggers();
                	}
                    
                    if ( this.itemType == 'action' ){
                    	new Actions();
                    }

                    // Check if the ID of the new item is defined
                    if ( isDefined( response.post_ID ) ){
                    	// Get item form element
                    	const $newItem = getDOMElements( 'item', {
                    		id: response.post_ID
                    	});

                    	// Get first token and trigger a click
                    	$newItem.item.find( '.item-title__token:not(.item-title__token--filled)' ).first().trigger( 'click' );
                    }
                }, ( response ) => {
                	// Check if it's an invalid action
                	if ( response.success == false ){
                		new Triggers();
                	}
                });
			});
		});

		tasks = $( tasks ).map( function(){ return this.get() });

		// Add "No Results" element
		let noResults = $( '<div/>', { 'class': 'item-task-selector__option item-task-selector__option--no-results item-task-selector__option--hide' });
		noResults.text( UncannyAutomator.i18n.noResults );
		listContainer.append( noResults );
		taskSelector.append( listContainer );

		formElement.append( taskSelector );
		itemOptions.append( formElement );

		// Cancel button
		itemOptions.append( this.createCancelButton() );

		container.append( itemOptions );

		// Bind search
        searchInput.on( 'input', function(){
            let value = searchInput.val();

            // Reset before search
            tasks.removeClass( 'item-task-selector__option--show item-task-selector__option--hide' );

            // Create variable to search if there were results
            let has_results = true;

            if ( ! isEmpty( value ) ){
                // If value it isn't empty then we have to check if there are going to be results, so false by default
                has_results = false;

                tasks.each( function(){
                    let $thisTask = $(this),
                        text 	  = $thisTask.text().toLowerCase().replace( /\s+/, '' );

                    value.toLowerCase().replace( /\s+/, '' );

                    if ( text.search(value) !== -1 ){
                        $thisTask.addClass( 'item-task-selector__option--show' );

                        // At least one result, so true
                        has_results = true;
                    }
                    else {
                        $thisTask.addClass( 'item-task-selector__option--hide' );
                    }
                });
            }

            // Show and hide noResults
            noResults.removeClass( 'item-task-selector__option--hide item-task-selector__option--show' );

            if ( ! has_results ){
                noResults.addClass( 'item-task-selector__option--show' );
            }
            else {
                noResults.addClass( 'item-task-selector__option--hide' )
            }
        });

		return container;
	}

	createCancelButton(){
		let cancelButton = $( '<div/>', { 'class': 'btn btn--secondary' }).text( UncannyAutomator.i18n.cancel );

		cancelButton.on( 'click', () => {
			new ItemCreatorIntegrations( this.itemType, this.$item );
		});

		return cancelButton;
	}
}

export default itemCreatorTaskSelector;


