import {
    itemsObject,
    getDOMElements,
    isDefined,
    vsprintf
} from './Utilities.js';
import ItemHeaderMenu          from './ItemHeaderMenu.js';
import itemCreatorTaskSelector from './itemCreatorTaskSelector.js';

class ItemCreatorIntegrations {
    constructor( itemType, $currentItem ){
        this.itemType     = itemType;
        this.$currentItem = $currentItem;

        // Render DOM elements
        this.create();
    }

    create(){
        // Create container
        let container = $( '<div/>', { 'class': 'item' });
        this.$item    = container;

        let header       = this.createHeader(),
            itemContent  = $( '<div/>', {'class': 'item__content' }),
            integrations = this.createIntegrations();

        container.append( header );

        itemContent.append( integrations );
        container.append( itemContent );

        // Check if the $item is defined, in that case we're going to replace the item element instead of creating a new one
        if ( isDefined( this.$currentItem ) ){
            // Empty container
            this.$currentItem.replaceWith( container );
        }
        else {
            // Get last item and append next to it
            let lastItem = getDOMElements( 'lastItem', {
                type: this.itemType
            });

            if ( this.itemType == 'trigger' ){
                // Check if there is a last item
                if ( lastItem.length > 0 ){
                    container.insertAfter( lastItem );
                }
                else {
                    // Otherwise append the item to the metabox
                    let metabox = getDOMElements( 'metabox', {
                        type: this.itemType
                    });

                    metabox.append( container );
                }
            }
            
            if ( this.itemType == 'action' ){
                // Check if there is at least one item
                let items = getDOMElements( 'items', {
                    type: this.itemType
                });

                if ( items.length > 0 ){
                    // Insert after the last item
                    container.insertAfter( lastItem );
                }
                else {
                    // Otherwise replace the itemCreatorAdd container with the new container
                    let metabox = getDOMElements( 'metabox', {
                        type: this.itemType
                    });
                    let itemCreatorAdd = metabox.find( '.item.item--start' );
                    itemCreatorAdd.replaceWith( container );
                }
            }
        }
    }

    createHeader(){
        let header  = $( '<div/>' );

        let icon           = $( '<div/>', { 'class': 'item-icon' }),
            titleContainer = $( '<div/>', { 'class': 'item-title clear' }),
            titleContent   = $( '<div/>', { 'class': 'item-title__describe' }),
            title          = $( '<div/>', { 'class': 'item-title__non-editable' }).text( UncannyAutomator.i18n.integration.select );

        let actions        = new ItemHeaderMenu( this.itemType );
        header.append( actions );

        header.append( icon );

        titleContent.append( title );
        titleContainer.append( titleContent );
        header.append( titleContainer );

        return header;
    }

    createIntegrations(){
        let container    = $( '<div/>', { 'class': 'item-integrations' });

        // Get non empty integrations
        let integrations = this.getNonEmptyIntegrations( this.itemType );
        $.each( integrations, ( integrationId, integration ) => {
            // Create integration element
            let integrationElement = this.createIntegration( integrationId, integration );

            // Bind
            integrationElement.on( 'click', () => {
                new itemCreatorTaskSelector( this.itemType, integrationId, this.$item );
            });

            // Append
            container.append( integrationElement );
        });

        // Return jQuery element
        return container;
    }

    createIntegration( integrationId, integration ){
        let container   = $( '<div/>', { 'class': 'item-integration' }),
            logo        = $( '<div/>', { 'class': 'item-integration__logo' }),
            name        = $( '<div/>', { 'class': 'item-integration__name' });

        // Create the variable where we're going to save the logo
        let $logoImg;

        // Check if it has a svg image
        if ( isDefined( integration.logo_svg ) ){
            $logoImg = $( `<img src="${ integration.logo_svg }" alt="${ integration.name }">` );
        }
        // Check if it has a png image
        else if ( isDefined( integration.logo ) && isDefined( integration.logo_retina ) ){
            $logoImg = $( `<img src="${ integration.logo }" srcset="${ vsprintf( '%s 1x, %s 2x', [ integration.logo, integration.logo_retina ]) }" alt="${ integration.name }">` );
        }

        // Set integration name
        name.text( integration.name );

        // Append
        logo.append( $logoImg );
        container.append( logo );
        container.append( name );

        // Return jQuery element
        return container;
    }

    getNonEmptyIntegrations( itemType ){
        let integrations = {}

        // Get items by itemType
        let items = itemsObject( 'get', {
            filter: {
                itemType: itemType
            }
        });

        // Search for non empty integrations
        let globalItems;

        if ( itemType == 'trigger' ){
            globalItems = UncannyAutomator.triggers;
        }

        if ( itemType == 'action' ){
            globalItems = UncannyAutomator.actions;
        }

        $.each( globalItems, function( index, globalItem ){
            let integrationId = globalItem.integration;

            if ( ! isDefined( integrations[ integrationId ] ) ){
                integrations[ integrationId ] = UncannyAutomator.integrations[ integrationId ];
            }
        });

        // Return integrations
        return integrations;
    }
}

export default ItemCreatorIntegrations;


